import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useHistory } from "react-router-dom";
import M from 'materialize-css';
const axios = require('axios');

const api = process.env.REACT_APP_API

function Voting(props) {
  const votes = props.votes;
  const [vote, voteSet] = useState('');

  const castVote = n => {
    if(n !== vote){
      props.castVote(n);
      voteSet(n);
    }
  }

  return (
    <div className="">
      <div className="section"></div>
      {props.admin ?
        <div className="row">
          <div className="col s12 center-align">
            <button className="btn grey darken-1" onClick={props.endGame}>End Game</button>
          </div>
        </div>
      : null}
      {Object.keys(votes).map((name, i) =>
        <div className="row" key={i}>
          <div className="col m3 hide-on-small-only"></div>
          <div className="col s8 m4">
            <h4 className="truncate">{name}</h4>
          </div>

          <div className="col s1 m1">
            <h4>{votes[name].length}</h4>
          </div>

          <div className="col s3 m1 right-align" style={{marginTop: '25px'}}>
            <button className="btn-small grey darken-1" onClick={() => castVote(name)}>Vote</button>
          </div>
          <div className="col m3 hide-on-small-only"></div>
        </div>
      )}
    </div>
  );
}

export default Voting;
