import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
const axios = require('axios');

const api = process.env.REACT_APP_API

function Home() {
  const [games, setGames] = useState([]);

  useEffect(() => {
    axios.get(api + '/game')
    .then(r => setGames(r.data));
  }, []);


  return (
    <div className="container">
      <h3>スパイ</h3>
      <div className="section"></div>
      <div className="row center-align">
        <Link to='/create' className="btn-small grey darken-1">Create</Link>
      </div>
      <div className="row">
        {games.map(name =>
          <div className="col s12" key={name}>
            <div className="col s1 m3"></div>
            <div className="col s5 m3">
              <h4>{name}</h4>
            </div>
            <div className="col s5 m3 right-align" style={{marginTop: '25px'}}>
              <Link to={'/'+name} className="btn-small grey darken-1">Join</Link>
            </div>
            <div className="col s1 m3"></div>
          </div>
        )}
      </div>
    </div>
  );
}

export default Home;
