import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useHistory } from "react-router-dom";
import M from 'materialize-css';
const axios = require('axios');

const api = process.env.REACT_APP_API

function Started(props) {
  const ends = props.ends;
  const places = props.places;
  const [highlights, highlightsSet] = useState(Object.assign({},places));
  const [test, testSet] = useState(true);


  useEffect(() => {
    const collapsible = document.querySelectorAll('.collapsible');
    M.Collapsible.init(collapsible);
    intrvl();
    return () => {
      clearInterval(x);
    };
  });

  const zeroPad = (num, places) => {
    const zero = places - num.toString().length + 1;
    return Array(+(zero > 0 && zero)).join("0") + num;
  }

  const intrvl = () => {
    const distance = ends - Math.floor(Date.now() / 1000)
    document.getElementById('time').innerHTML = zeroPad(Math.floor(distance / 60), 2) + ':' + zeroPad(Math.floor(distance % 60), 2);
    if (distance <= 0) {
      clearInterval(x);
      document.getElementById('time').innerHTML = '00:00';
    }
  }

  const x = setInterval(intrvl, 1000);

  const highlight = n => {
    let tmp = Object.assign({},highlights);
    tmp[n] = !tmp[n];
    highlightsSet(tmp);
  }

  return (
    <div className="container">
      <ul className="collapsible" id="collapsible">
        <li>
          <div className="collapsible-header background1F"><i className="material-icons">assignment_ind</i>Your Role</div>
          <div className="collapsible-body background1F"><span>{props.place}</span></div>
        </li>
      </ul>
      {props.admin ?
        <div className="row">
          <div className="col s12 center-align">
            <button className="btn grey darken-1" onClick={props.startVoting}>Start Voting</button>
          </div>
        </div>
      : null}
      <div className="row center-align">
        <h5 id="time"></h5>
      </div>
      <div className="row">
        {Object.keys(places).map((name, i) => {
          if(places[name]){
            return (
              <div className="col s6 m4 center-align" key={i}>
                <div
                  className={highlights[name] ? 'pactive truncate' : 'pnotactive truncate'}
                  style={{margin: '15px 10px', padding: '10px 3px', borderRadius: '2px'}}
                  onClick={() => highlight(name)}
                >
                  {name}
                </div>
              </div>
            )
          }
        })}
      </div>
    </div>
  );
}

export default Started;
