import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useHistory } from "react-router-dom";
import M from 'materialize-css';
const axios = require('axios');

const api = process.env.REACT_APP_API

function PreGame(props) {

  useEffect(() => {
    const slider = document.getElementById('slider');
    M.Range.init(slider);
    slider.value = props.time;
  }, []);

  return (
    <div className="container">
      <div className="section"></div>
      <div className="row">
        <h6>Players</h6>
        {props.players.map(player =>
          <div className="col s6 m4 center-align" key={player}>
            <div className="teal lighten-1" style={{margin: '15px 10px', padding: '3px', borderRadius: '2px'}}>
              {player}
            </div>
          </div>
        )}
      </div>
      {props.admin ?
        <div className="row">
          <div className="col s12 center-align">
            <button className="btn grey darken-1" onClick={props.startGame}>Start</button>
          </div>
        </div>
      : null}
      <div className="row">
        <h6>Time</h6>
        <p className="range-field">
          <input type="range" id="slider" min="1" max="20" value={props.time} onChange={e => props.updateTime(e.target.value)} />
        </p>
      </div>
      <div className="row">
        <h6>Locations</h6>
        <div className="row">
          <div className="input-field col s6 offset-s2">
            <input id="place" type="text" autoComplete="off" />
            <label htmlFor="place">New Location</label>
          </div>
          <button className="btn grey darken-1" style={{marginBottom: '-50px'}} onClick={props.addPlace}>Add</button>
        </div>
        {Object.keys(props.places).map((name, i) =>
          <div className="col s6 m4 center-align" key={i}>
            <div
              className={props.places[name] == 1 ? 'pactive truncate' : 'pnotactive truncate'}
              style={{margin: '15px 10px', padding: '10px 3px', borderRadius: '2px'}}
              onClick={() => props.updatePlace(name)}>
              {name}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default PreGame;
