import React, { useState, useEffect } from 'react';
import { Link, BrowserRouter } from 'react-router-dom';
import useStateWithCallback from 'use-state-with-callback';
import M from 'materialize-css';
import PreGame from './Pregame';
import Started from './Started';
import Voting from './Voting';
const axios = require('axios');

const api = process.env.REACT_APP_API
const wsApi = process.env.REACT_APP_WS_API
let ws = null;

function Game(props) {
  const gameName = props.match.params.id;
  const [name, nameSet] = useState(localStorage.getItem('username') || '');
  const [players, playersSet] = useState([]);
  const [time, timeSet] = useState(0);
  const [admin, adminSet] = useState('');
  const [places, placesSet] = useState([]);
  const [ends, endsSet] = useState(0);
  const [stage, stageSet] = useState('');
  const [spy, spySet] = useState('');
  const [place, placeSet] = useState('');
  const [votes, votesSet] = useState({});

  useEffect(() => {
    // initiate stuff
    const modal = document.getElementById('modal');
    M.Modal.init(modal, {dismissible: false});

    if(name.length > 0){
      initWs();
    }else {
      M.Modal.getInstance(document.getElementById('modal')).open();
    }
  }, []);

  const setName = () => {
    const username = document.getElementById('name').value;
    if(username.length > 0){
      localStorage.setItem('username', username);
      M.Modal.getInstance(document.getElementById('modal')).close();
      nameSet(username);
      initWs();
    }
  }

  const initWs = () => {
    ws = new WebSocket("wss://rybieudko.pl/spy/ws");
    ws.onmessage = msg => onMessage(msg);
    ws.onopen = () => {
      let msg = JSON.stringify({'action': 'init', 'data':{'gameName': gameName, 'userName': localStorage.getItem('username')}})
      ws.send(msg);
    };
  }

  const onMessage = msg => {
    msg = JSON.parse(msg.data)
    if(msg.action == 'init'){
      const data = msg.data;
      playersSet(data.players);
      timeSet(data.time);
      adminSet(data.admin);
      placesSet(data.places);
      stageSet(data.stage);
      endsSet(data.ends);
      spySet(data.spy);
      placeSet(data.place);
      votesSet(data.votes);
    } else if(msg.action == 'update_players'){
      playersSet(msg.data.players)
    } else if(msg.action == 'start'){
      const data = msg.data;
      endsSet(data.ends);
      spySet(data.spy);
      placeSet(data.place);
      stageSet(data.stage);
      votesSet(data.votes);
    } else if(msg.action == 'update_place'){
      placesSet(msg.data.places)
    } else if(msg.action == 'vote'){
      votesSet(msg.data)
    } else if(msg.action == 'start_voting'){
      stageSet('voting')
    } else if(msg.action == 'end_game'){
      const data = msg.data;
      stageSet(data.stage);
      votesSet(data.votes);
      placeSet(data.place);
      spySet(data.spy);
      votesSet(data.votes);
    } else if(msg.action == 'update_time'){
      timeSet(msg.data.time)
    }
  }

  const startGame = () => {
    let msg = JSON.stringify({'action': 'start', 'data': {'game': props.match.params.id}})
    ws.send(msg)
  }

  const updatePlace = n => {
    let msg = JSON.stringify({'action': 'update_place', 'data': {'place': n, 'game': gameName}})
    ws.send(msg)
  }

  const addPlace = () => {
    const place = document.getElementById('place');
    if (place.value.length > 0){
      let msg = JSON.stringify({'action': 'add_place', 'data': {'place': place.value, 'game': gameName}})
      ws.send(msg)
      place.value = '';
      place.blur();
    }
  }

  const startVoting = () => {
    let msg = JSON.stringify({'action': 'start_voting', 'data': {'game': gameName}})
    ws.send(msg)
  }

  const castVote = n => {
    let msg = JSON.stringify({
      'action': 'vote',
      'data': {'game': gameName, 'user': name, 'vote': n}
    })
    ws.send(msg)
  }

  const endGame = () => {
    let msg = JSON.stringify({'action': 'end_game', 'data': {'game': gameName}})
    ws.send(msg)
  }

  const updateTime = t => {
    let msg = JSON.stringify({'action': 'update_time', 'data': {'game': gameName, 'time': t}})
    ws.send(msg)
  }


  return (
    <div>
      {stage == 'pregame' ?
        <PreGame
          players={players}
          admin={admin == name}
          places={places}
          startGame={startGame}
          addPlace={addPlace}
          updatePlace={updatePlace}
          time={time}
          updateTime={updateTime}
        />
        : stage == 'started' ?
        <Started
          ends={ends}
          admin={admin == name}
          places={places}
          place={spy == name ? 'spy' : place}
          startVoting={startVoting}
        />
        : stage == 'voting' ?
        <Voting
          votes={votes}
          admin={admin == name}
          castVote={castVote}
          endGame={endGame}
        />
        : null
      }
      {name.length > 0 ? null :
      <div id="modal" className="modal" style={{backgroundColor: '#1F1F1F'}}>
        <div className="modal-content">
          <div className="section"></div>
          <div className="input-field">
            <input id="name" type="text" className="validate" autoComplete="off" />
            <label for="name">Your Name</label>
          </div>
          <div className="row">
            <div className="col s12 center-align">
              <a className="waves-light btn grey darken-1" onClick={setName}>Set name</a>
            </div>
          </div>
        </div>
      </div>
      }
    </div>
  );
}

export default Game;
