import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useHistory } from "react-router-dom";
import M from 'materialize-css';
const axios = require('axios');

const api = process.env.REACT_APP_API

function Create(props) {
  let history = useHistory();

  const createGame = () => {
    const gameName = document.getElementById('name').value;
    const admin = document.getElementById('nick').value;
    props.setName(admin);
    axios({
      method: 'post',
      url: api + '/game',
      data: {
        name: gameName,
        admin: admin
      },
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      }
    }).then(res =>  history.push('/' + gameName))
    localStorage.setItem('username', admin)
  }


  return (
    <div className="container">
      <div className="section"></div>
      <div className="input-field">
        <input id="name" type="text" className="validate" autoComplete="off"/>
        <label htmlFor="name">Game Name</label>
      </div>
      <div className="input-field">
        <input id="nick" type="text" className="validate" autoComplete="off"/>
        <label htmlFor="nick">Your Name</label>
      </div>
      <div className="row">
        <div className="col s12 center-align">
          <a className="waves-light btn grey darken-1" onClick={createGame}>Create</a>
        </div>
      </div>
    </div>
  );
}

export default Create;
